@mixin plus-icon()
{
  position: relative;
  
  &:after {

    content: "";
    position: absolute;
    top: calc(50% - #{rem(32/2)});
    left: auto;
    right: rem(8);
    height: rem(32);
    width: rem(32);
    transition: transform 0.25s ease-out;

    @if $compatible == 'true' {

      background-image: escape-svg($plus-svg);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-color: transparent!important;
    }
    @else {
      mask-image: var(--icon-plus);
      mask-size: 100% 100%;
      mask-repeat: no-repeat;
      mask-position: 0 0;
    
      -webkit-mask-image: var(--icon-plus);
      -webkit-mask-size: 100% 100%;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-position: 0 0;
      background-color: currentColor;
    }
  }

  &:not(.collapsed):after {
    transform: rotate(45deg);
  }
}


@mixin search-icon($size: 32)
{

  &:after {

    content: "";
    position: absolute;
    top: calc(50% - #{rem($size/2)});
    left: auto;
    right: calc(50% - #{rem($size/2)});
    height: rem($size);
    width: rem($size);
    transition: transform 0.25s ease-out;

    @if $compatible == 'true' {

      background-image: escape-svg($search-svg);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-color: transparent!important;
    }
    @else {
      mask-image: var(--icon-search);
      mask-size: 100% 100%;
      mask-repeat: no-repeat;
      mask-position: 0 0;
    
      -webkit-mask-image: var(--icon-search);
      -webkit-mask-size: 100% 100%;
      -webkit-mask-repeat: no-repeat;
