
@mixin pattern-dimensions($scale: 1)
{
  $assetHeight: 320;
  $assetWidth: 1600;

  $height: $assetHeight*$scale;
  $width: $assetWidth*$scale;

	height: rem($height*0.5);
  width: rem($width*0.5);


  @if $scale < 1 {
    @include var(stroke-width,--pattern-stroke-sm);
  }
  @else if $scale == 1 {
    @include var(stroke-width,--pattern-stroke-default);
  }
  @else if $scale > 1 and $scale <= 2 {
    @include var(stroke-width,--pattern-stroke-lg);
  }
  @if $scale > 2 {
    @include var(stroke-width,--pattern-stroke-xl);
  }
  
  @include media-breakpoint-up(sm) {

    height: rem($height*0.75);
    width: rem($width*0.75);
  }
  @include media-breakpoint-up(md) {

    height: rem($height);
    width: rem($width);

  }
}

@mixin pattern-colour($key,$compatible: $compatible)
{
  @if $compatible == 'true' {
    &:not([class*="stroke-"]) .pattern__inner svg {
      stroke: map-get($vars, $key)!important;
    }
  }
  @else {

    --pattern-colour: var(#{$key});
  }
}

