

// Grid Breakpoints 
$grid-breakpoints: (
  xs: 0,
  sm: 36em, /* 576 */
  md: 62em  /* 992 */
);

$device-xs-width: 375;
$device-sm-width: 768;
$device-md-width: 1440;


$container-max-widths: (
  sm: 0,
  md: 80em
);


$container-max-width: 1440; // The width the backgrounds will go to
$container-max-inner-width: 1200; // The width the content will go to


$grid-columns: 12;
$grid-gutter-width: 1rem; // 15px

$gutter: 0.5rem;

$container-padding: rem(32);
$container-sm-padding: rem(48);
$container-md-padding: rem(120);
$container--outer-inner-md-padding: 2rem;


$container-all-padding: rem(48) $container-padding 0 $container-padding;
$container-all-sm-padding: rem(64) $container-sm-padding rem(16) $container-sm-padding;
$container-all-md-padding: rem(80) $container-md-padding rem(32) $container-md-padding;

$col-8-width-decimal: (100 / ((8/$grid-columns) * 100));

$max-content-px: ($container-max-width - ($container-max-width - $container-max-inner-width)) / (100 / ((8/$grid-columns) * 100));
$max-content-width: #{rem($max-content-px)};


$col-10-width-decimal: (100 / ((10/$grid-columns) * 100));
$max-content-10col-px: ($container-max-width - ($container-max-width - $container-max-inner-width)) / $col-10-width-decimal;
$max-content-10col: #{rem($max-content-10col-px)};


//max conatiner--outer width minus the percentage width of col-8 of the inner containers padding
$max-content-width--container-outer: #{rem($max-content-px - (($container--outer-inner-md-padding * $base-font-size * 2) / $col-8-width-decimal)/1rem)};


