@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.gauge {
  
  &:not([class*="colour-"]){

    --colour: var(--colour-secondary);
  }

  --value-padding: #{rem(16)};
  max-width: rem(640);
  margin-bottom: rem(64);
  background-color: transparent!important;

  @include media-breakpoint-up(sm) {
    --value-padding: #{rem(24)};
  }

  &__progress,
  &__remaining {
    min-height: rem(96);

  }

  &__progress {

    border-left: 2px solid var(--colour);
    padding-left: var(--value-padding);
    margin-bottom: rem(8 + 16 + 8);
    position: relative;

    > * {

      max-width: rem(200 - 24);
    }

    > *:last-child {
      padding-bottom: 0;
      margin-bottom: rem(16);
    }

    &:before,
    &:after {

      content: "";
      display: block;
      height: rem(16);
      position: absolute;
      top: calc(100% + #{rem(8)});
      left: -2px;
    }
    &:before {
      width: calc(100% + 2px);
      background: var(--colour-border);
    }

    &:after {
      width: calc(var(--percent) + 2px);
      background: var(--colour);
      z-index: 2;
    }
  }

  &__remaining {
    text-align: right;
    margin-left: auto;
    border-right: 2px solid var(--colour-border);
    padding-right: var(--value-padding);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    max-width: rem(200);

    > *:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
}

@supports (background: conic-gradient(red, blue)) {
  
  .gauge--radial {

    --pie-size: #{rem(200)};
    --pie-thickness: #{rem(16)};

    @include media-breakpoint-up(sm) {
      --pie-size: #{rem(240)};
    }
    @include media-breakpoint-up(md) {
      --pie-thickness: #{rem(24)};
    }

    .gauge__progress {

      border: none;
      padding: var(--pie-thickness);
      margin-bottom: 0;
      background: radial-gradient( circle closest-side, transparent 100%, white 0 ), conic-gradient(var(--colour) 0, var(--colour) var(--percent), var(--colour-border) 0, var(--colour-border) 100%);
      width: var(--pie-size);
      height: var(--pie-size);
      margin-left: auto;
      margin-right: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      > * {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        display: block;
        z-index: 2;
        position: relative;
      }

      > *:last-child {
        padding-bottom: 0;
        margin-bottom: 0;
      }

      &:before {
        background: white;
        top: var(--pie-thickness);
        left: var(--pie-thickness);
        height: calc(100% - var(--pie-thickness) - var(--pie-thickness));
        width: calc(100% - var(--pie-thickness) - var(--pie-thickness));
        border-radius: 50%;
        z-index: 1;
      }

      &:after {
        display: none;
      }
    }

    &:not(.gauge--radial-remaining){

      .gauge__remaining {
        display: none;
      }
    }
    &.gauge--radial-remaining{

      .gauge__remaining {
        
        min-height: 0;
        border: none;
        padding-right: 0;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        padding-top: rem(24);
      }
    }
  }
}