$animation_offset: #{rem(160)};
$motion: ();
$motion: map-merge((
  "fade-in": 1.25s ease-out fade-in forwards,
  "fade-out": 1.25s ease-out fade-out forwards,
  "fade-in-img": 1.25s ease-out fade-in 0.25s forwards,
  "fade-out-img": 1.25s ease-out fade-out 0.25s forwards,
  "slide-up": 0.5s ease-out slide-up forwards,
  "slide-right": 0.5s ease-out slide-right forwards,
  "slide-right-out": 0.5s ease-out slide-right-out forwards,
  "slide-down": 0.5s ease-out slide-down forwards,
  "slide-left": 0.5s ease-out slide-left forwards,
  "slide-left-out": 0.5s ease-out slide-left-out forwards,
  "fade-up": "1.5s ease-out fade-in  forwards, 1.5s ease-out slide-up  forwards",
  "fade-right": "1.5s ease-out fade-in  forwards, 1.5s ease-out slide-right  forwards",
  "fade-down": "1.5s ease-out fade-in  forwards, 1.5s ease-out slide-down  forwards",
  "fade-left": "1.5s ease-out fade-in  forwards, 1.5s ease-out slide-left  forwards",
  "draw-in": var(--pattern-animation-speed, 2s) ease-out draw-in forwards,
  "draw-out": var(--pattern-animation-speed, 2s) ease-out draw-out forwards
), $motion);

//Transitions
$transition-base:             all .2s ease-in-out;
$transition-fade:             opacity .15s linear;
$transition-collapse:         height .35s ease;