@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region Chart Colours
$chart-colors: ();
$chart-colors: map-merge((
  1: --colour-secondary,
  2: --colour-secondary-2,
  3: --colour-secondary-4,
  4: --colour-secondary-3,
  5: --colour-accent,
  6: --colour-success,
  7: --colour-secondary,
  8: --colour-secondary-2,
  9: --colour-secondary-4,
  10: --colour-secondary-3,
  11: --colour-accent,
  12: --colour-success
), $chart-colors);

.container--chart {
  
  @each $index, $value in $chart-colors {
    --colour-chart-#{$index}: var(#{$value});
  }
}

@mixin chart-colour($property,$index) {

  @if $compatible == 'true' {
    @include var($property,	#{map-get($chart-colors, $index)},!important);
  }
  @else {

    #{$property}: var(--colour-chart-#{$index})!important;
  }
}

@for $i from 1 to 13 {
  .chart-colour-#{$i} {
    @include chart-colour(color,$i);
  }
}
// #endregion

// #region Chart Key
.chart__key {

  padding-bottom: rem(16);
  margin-left: rem(-16);
  margin-right: rem(-16);

  @include media-breakpoint-up(sm) {
    margin-left: rem(-24);
    margin-right: rem(-24);
  }

  @include media-breakpoint-up(md) {
    margin-left: 0;
    margin-right: 0;
  }

  &:after {
    display: table;
    clear: both;
    content: "";
  }

  .key__title {
    display: block;
    max-width: 100%;
  }

  .custom-checkbox {
    
    @include var(color,--colour-body);
    
    width: 50%;
    float: left;

    @include media-breakpoint-up(sm) {
      width: auto;
      max-width: 50%;
      padding-right: rem(64);
    }

  }

  .custom-control-input[type="checkbox"] {
    pointer-events: none;
  }

  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ .custom-control-label:before {
    border-color: currentColor;
    background: currentColor;
  }

  .key__btn > span {
    content: "";
    position: absolute;
    display: inline-block;
    top: 0;
    left: 0;
    height: 1em;
    line-height: 1.1em;
    width: 1em;
    margin-bottom: -0.1em;
    margin-right: 0.5em;
    background: currentColor;
  }


}

.js-enabled .chart__key {

  .custom-control-input[type="checkbox"] {
    pointer-events: inherit;
  }
}


// #endregion

// #region Chart wrapper
.chart__wrapper {
  
  margin-top: rem(16);
  position: relative;
  display: block;

  margin-left: rem(-16);
  margin-right: rem(-16);
  margin-bottom: rem(64);

  @include media-breakpoint-up(sm) {
    margin-left: rem(-24);
    margin-right: rem(-24);
  }

  @include media-breakpoint-up(md) {
    margin-left: 0;
    margin-right: 0;
  }
}
// #endregion

// #region Chart
.chart {

  display: flex;
  position: relative;
  width: 100%;
  padding-top: 56.25%; // 16:9 ratio
  margin-bottom: rem(24);

  @include media-breakpoint-up(sm) {

    padding-top: 42.86%; // 21:9 ratio
    margin-bottom: 0;
  }

  .chart__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;

    @include media-breakpoint-up(sm) {
      height: calc(100% - #{rem(24)});
    }
  }
  
  .chart__label {
    font-size: rem(14);
    line-height: rem(24);
    @include var(color,--colour-body);
  }
  
  // - Y-axis and guidelines
  .chart__y-axis {

    text-align: right;
    position: relative;
    display: flex;
    flex-flow: column-reverse;
    width: 0;
    margin-bottom: 2px;
    

    @include media-breakpoint-up(sm) {

      width: auto;
      padding-right: rem(8);
    }

    @include media-breakpoint-up(md) {
      margin-bottom: 3px;
    }

    .group {
      flex-grow: 0;
      flex-shrink: 0;
      height: var(--percent);

      .chart__label {
        display: none;
      }

      &:last-child {
        .chart__label {
          display: block;
          @include prefix(transform, translate(0,-100%));
        }
      }

      @include media-breakpoint-up(sm) {
        .chart__label {
          display: block;
          @include prefix(transform, translate(0,-50%));
        }

        &:last-child {
          .chart__label {
            @include prefix(transform, translate(0,-50%));
          }
        }
      }
    }
  }

  // - chart guidelines
  .chart__guidelines {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-flow: column-reverse;

    .guideline {
      flex-grow: 0;
      flex-shrink: 0;
      height: var(--percent);
      @include var(color,--colour-border);
      border-top: 1px solid currentColor;
      margin: 0;

      &:first-child {
        display: none;
      }
    }
  }

  // - X-axis
  .chart__x-axis {

    width: 100%;
    height: 100%;
    position: relative;
    display: block;
    @include var(color,--colour-border);
    border-bottom: 2px solid currentColor;

    @include media-breakpoint-up(md) {
      
      border-bottom: 3px solid currentColor;
    }
  }

  .chart__x-axis ul {
    display: flex;
    margin: 0;
    padding: 0;
    height: 100%;
    max-width: none;

    .group {
      height: 100%;
      flex: 0 1 100%;
      display: flex;

      -ms-flex-align: end!important;
      align-items: flex-end!important;
      -ms-flex-pack: center!important;
      justify-content: center!important;

      .chart__label {
        position: absolute;
        top:100%;

        padding-top: rem(8);
        left: 50%;

        @include prefix(transform, translate(-50%,0));
        min-width: 100%;
        text-align: center;
        display: none;
      }

      @include media-breakpoint-up(sm) {
        .chart__label {
          display: block;
          white-space: nowrap;
        }
      }
    }

  }

  .chart__x-axis--mobile {

    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    
    .chart__label {
      float: left;
      padding-top: rem(8);
    }

    .chart__label + .chart__label{
      float: right;
    }

    @include media-breakpoint-up(sm) {
      
      display: none;
    }
  }


  // - data item (point,bar etc)

  .data__wrapper {
    display: flex;
    height: 100%;
    width: 100%;
    -ms-flex-align: end !important;
    align-items: flex-end !important;
    -ms-flex-pack: center !important;
    justify-content: center !important;
  }

  .data {

    -ms-flex: 0 1 100%;
    flex: 0 1 100%;
    height: 100%;
    height: var(--percent);
    margin-left: rem(2);
    margin-right: rem(2);
    position: relative;
    outline: none;
    border-radius: rem(4);
    border: none;

    z-index: 2;
    max-width: rem(48);
    

    @include media-breakpoint-up(sm) {

      max-width: rem(64);
    }

    @include media-breakpoint-up(md) {

      max-width: rem(96);
    }

    &:hover {
      z-index: 10;
    }

    &:focus,
    &:active {
      z-index: 20;
    }

    background: currentColor;

    [data-tooltip] {
      width: auto;
      max-width: rem(200);
      text-align: center;
      z-index: 99;

      @include media-breakpoint-up(md) {
        left: 50%!important;
        right: auto!important;
        transform: translate(-50%, 0)!important;
      }

      span,
      strong {
        display: block;
      }
    }
  }

  .data__label {
    position: absolute;
    bottom: 100%;
    opacity: 0;
    pointer-events: none;
  }

}
// #endregion

// #region Line chart 
.chart--line .chart{

  .chart__x-axis ul .group {

    .chart__label {
      text-align: left;
      min-width: auto;
      left: 0;
    }
    
    &:first-child {
      
        
      .chart__label {
        -webkit-transform: translate(0%, 0);
        -ms-transform: translate(0%, 0);
        -o-transform: translate(0%, 0);
        transform: translate(0%, 0);
      }
    }

    
    &:last-child {
      max-width: 1px;
      .chart__label {
        left: auto;
        right: 0;
        -webkit-transform: translate(0%, 0);
        -ms-transform: translate(0%, 0);
        -o-transform: translate(0%, 0);
        transform: translate(0%, 0);
      }
    }
  }

  .data {
    height: rem(8);
    width: rem(8);
    flex: none;
    margin: 0 0 rem(-4) rem(-4);
    position: absolute;
    bottom: var(--percent);
    left: 0;
    opacity: 0;
    z-index: 20;

    &:hover {
      z-index: 30;
    }

    background: currentColor;
    border-radius: 50%;

    @include media-breakpoint-up(sm) {

      height: rem(16);
      width: rem(16);
      margin: 0 0 rem(-8) rem(-8);
    }

  }

  .marker {
    width: 0;
    @include var(color,--colour-border);
    border-left: 1px dashed currentColor;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0%;
    opacity: 0;
    z-index: 0;
  }

  .data:hover {

    opacity: 1!important;

    & .marker {

      opacity: 1;
    }
  }

  .group:hover .marker {
    opacity: 1;
  }


  .line,
  .range {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index:1;
    overflow: visible;
    pointer-events: none;

    path{
      stroke: currentColor;
      stroke-width: 0.6px; // ROUGHLY translate to 1px on mobile, 2px on tablet and 3px for desktop
      //transition: d 5s; // - Maybe needed at a later date when we add in load in transitions
      //stroke-linecap: round;
      stroke-linejoin: round;

      @include media-breakpoint-up(sm) {

        stroke-width: 0.5px; 
      }
      @include media-breakpoint-up(md) {

        stroke-width: 0.4px; 
      }
  
    }

    &.invert path {
      color: white!important;
    }
  }

  .line--dashed {
    stroke-dasharray: 1.5px;
    @include media-breakpoint-up(sm) {

      stroke-dasharray: 0.75px; 
    }
  }

  .line--thin {

    path{
      stroke-width: 0.3px; // ROUGHLY translate to 1px on mobile, 2px on tablet and 3px for desktop
      stroke-linecap: initial;

      @include media-breakpoint-up(sm) {

        stroke-width: 0.25px; 
      }
      @include media-breakpoint-up(md) {

        stroke-width: 0.2px; 
      }
    }
  }


  .range {

    z-index: 0!important;

    & path {
      stroke: none!important;
      fill: currentColor;
      opacity: 0.5;
    }

    &.show path {
      opacity: 1;
    }
  }


}

.chart--line.show-points .chart {
  .data:not(:hover):not(:focus):not(:active) {
    opacity: 1;
    transform: scale(0.5);
  }
}
// #endregion

// #region Stacked bar chart
.chart--stacked {

  .chart__key .custom-checkbox {
    pointer-events: none;
  }
  .custom-control-label:after {
    display: none;
  }
  
  .chart {

  .data__wrapper {

    flex-flow: column-reverse;
    justify-content: flex-start !important;
    align-items: center !important;
    }

    .data {
      width: 80%;
      
      flex: 0 0 var(--percent);
      border-radius: 0;
    }
  }
}
// #endregion

// #region Pie chart
.chart--pie .chart__wrapper{
  margin-left: 0;
  margin-right: 0;
}
.chart--pie .chart {
   padding-top: 0;
   
  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  @include media-breakpoint-up(sm) {

    justify-content: space-between;
  }
}

.chart--pie .pie{
  height: rem(272);
  width: rem(272);
  flex: 0 0 rem(272);
  position: relative;

  &:after {
    content: "";
    display: block;
    z-index: 5;
    position: absolute;
    top: 15%;
    left: 15%;
    height: 70%;
    width: 70%;
    border-radius: 50%;
  }

  .hotspot {
    position: absolute;
    opacity: 0;
    top: 0;
    left: 50%;
    height: 50%;
    width: 50%;
    transform-origin: bottom left;
    z-index: 4;
    border-top-right-radius: 100%;

    &--inner {
      position: absolute;
      display: block;
      top:0;
      left: 0;
      width: 100%;
      height: 100%;
      transform-origin: bottom left;
      border-top-right-radius: 100%;
    }
  }
  
  .tooltip {
    top: 15%;
    left: 50%;

    [data-tooltip]{
      width: auto;
      white-space: nowrap;
      transform: translate(-50%, 1rem);
    }
  }
  
  .hotspot:hover + .tooltip {
    [data-tooltip]{
      opacity: 1;
      visibility: visible;
    }
  }

  .tooltip[data-quarter="2"],
  .tooltip[data-quarter="3"] {
  
    top: 50%;
  }

  .hotspot:last-of-type + .tooltip {
    top: 15%;
  }



  @include media-breakpoint-up(md) {

    .tooltip[data-quarter="1"] {
  
      position: absolute;
      top: 2%;
      left: 75%;

      [data-tooltip]{
        transform: none;
      }
    }

    .tooltip[data-quarter="2"] {
  
      position: absolute;
      top: 80%;
      left: 75%;

      [data-tooltip]{
        transform: none;
      }
    }


    .tooltip[data-quarter="3"] {
  
      position: absolute;
      top: 80%;
      right: 75%;
      left: auto;

      [data-tooltip]{
        transform: none;
        left: auto;
        right: 0;
      }
    }


    .tooltip[data-quarter="4"],
    .hotspot:last-of-type + .tooltip {
  
      position: absolute;
      top: 2%;
      right: 75%;
      left: auto;

      [data-tooltip]{
        transform: none;
        left: auto;
        right: 0;
      }
    }
  }

  .hotspot.hotspot--small {
    z-index: 1;
  }
  .hotspot.hotspot--xxsmall {
    z-index: 2;
    width: 15%;
  }
  .hotspot.hotspot--xsmall {
    z-index: 2;
    width: 27%;
    border-top-right-radius: 70%;
  }
  .hotspot:last-of-type {
    z-index: 3;
  }

  .hotspot:nth-of-type(1):hover ~ svg circle:nth-child(1),
  .hotspot:nth-of-type(2):hover ~ svg circle:nth-child(2),
  .hotspot:nth-of-type(3):hover ~ svg circle:nth-child(3),
  .hotspot:nth-of-type(4):hover ~ svg circle:nth-child(4),
  .hotspot:nth-of-type(5):hover ~ svg circle:nth-child(5),
  .hotspot:nth-of-type(6):hover ~ svg circle:nth-child(6),
  .hotspot:nth-of-type(7):hover ~ svg circle:nth-child(7),
  .hotspot:nth-of-type(8):hover ~ svg circle:nth-child(8),
  .hotspot:nth-of-type(9):hover ~ svg circle:nth-child(9),
  .hotspot:nth-of-type(10):hover ~ svg circle:nth-child(10),
  .hotspot:nth-of-type(11):hover ~ svg circle:nth-child(11) {

    stroke-width: var(--stroke-width-hover,4px);
  }

  svg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;

    circle {
      stroke-width: 3px;
      pointer-events: all;
      stroke: currentColor;
    }
  }

  .group__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    pointer-events: none;
  }
}

// Slight changes depending upon number of pie's
@include media-breakpoint-up(sm) {

// If only one pie then display the key to the right
  .chart--pie[data-group-count="1"] {

    .chart__wrapper {
      width: rem(300);
      margin:0;
      float: left;
    }

    .chart__key {
      pointer-events: none;
      padding-top: rem(32);

      width: calc(100% - #{rem(332)});
      margin:0;
      float: right;

      .custom-checkbox {
        width: 100%;
      }
    }
  
  }


  // If only two pies then space out the charts differently
  .chart--pie[data-group-count="2"] .chart {

    justify-content: start;

    .pie:first-child {
      margin-right: rem(64);
    }
  }
}
// #endregion





// #region disable the key
.chart--stacked,
.chart--pie,
.chart--basic {

  .chart__key .custom-checkbox {
    pointer-events: none;
  }
  .custom-control-label:after {
    display: none;
  }
}
.chart--line.chart--basic {
  .chart__key .custom-control {
    width: auto;
    min-width: 50%;
  }
  .chart__key .custom-control-label:before {
    height: 2px;
    margin-top: calc(0.75rem - 1px);
    background: none!important;
    border: none;
    border-bottom: 2px solid currentColor;
  }
  .chart__key .line--dashed .custom-control-label:before {
    border-bottom: 2px dotted currentColor;
  }
  .chart__key .line--thin .custom-control-label:before {
    border-bottom: 1px solid currentColor;
  }
  @include media-breakpoint-up(sm) {
    .chart__key {
      padding-left: rem(16*3.5);
      position: relative;
    }
    .chart__key .custom-control {
      width: auto;
      min-width: 0;
    }
    .chart__key .key__title {
      position: absolute;
      top: 0;
      left: 0;
      max-width: none;
      width: auto;
    }
    .chart__key .custom-checkbox {
      padding-right: rem(16*2);
    }
  }
}
// #endregion



// #region Interaction overrides
.chart {

  .group .data.hide {
    pointer-events: none;
    opacity: 0;
  }

  .line.hide {
    opacity: 0;
  }
}
// #endregion

// #region Utility classes 

.container--chart--no-key {
  .chart__key {
    display: none;
  }
}

.chart--key-beneath {
  display: flex;
  flex-direction: column-reverse;

  .chart__key {
    margin-top: rem(-32);

    @include media-breakpoint-up(sm) {
      margin-top: rem(-16);
    }
    margin-bottom: rem(32);
  }
}

// #endregion
