//Body
$body-bg: $white;
$body-color: $colour-body;
$base-font-size: 16;

// Global
$border-color: $colour-border;

@if $compatible == 'false' {
  $border-color: var(--colour-border);
}
$border-width: 1px;
$border-radius: 50%;

$box-shadow-sm: transparent;
$box-shadow:    transparent;
$box-shadow-lg: transparent;

$component-active-color: inherit;
$component-active-bg:    $primary;

// Selectors
$dark-bg-selector-base: '[class*="bg-"]:not(.default-colours):not(.tick-list):not(.bg-secondary-2):not(.bg-secondary-4):not(.bg-light):not(.bg-success):not(.bg-info):not(.bg-warning):not(.bg-white)';

$dark-bg-selector: '.invert-colours, '+$dark-bg-selector-base;