// Create a big Variables map to use for CSS Variables fallback mixin *******************************************************************
$vars: ();

@each $color, $value in $theme-colors {

  $vars: map-merge((
    --colour-#{$color}: #{$value}
  ), $vars);
}

@each $color, $value in $non-theme-colors {

  $vars: map-merge((
    --colour-#{$color}: #{$value}
  ), $vars);
}
@each $color, $value in $alert-colors {

  $vars: map-merge((
    --colour-#{$color}: #{$value}
  ), $vars);
}

@each $bp, $value in $grid-breakpoints {

  $vars: map-merge((
    --breakpoint-#{$bp}: #{$value}
  ), $vars);
}

@each $font, $value in $fonts {

  $vars: map-merge((
    --font-#{$font}: #{$value}
  ), $vars);
}


@each $animation, $value in $motion {

  $vars: map-merge((
    --animation-#{$animation}: #{$value}
  ), $vars);
}

// Box shadow var
$vars: map-merge((
  --box-shadow: 0 0 #{rem(16)} 0 rgba(116,116,116,0.30)
), $vars);


$spacings: ();
$spacings: map-merge((
  "xs":   $spacing-xs,
  "sm":   $spacing-sm,
  "md":   $spacing-md
), $spacings);

@each $key, $value in $spacings {

  $vars: map-merge((
    --spacing-#{$key}: #{$value}
  ), $vars);


}

$vars: map-merge((
    --max-content-width: #{$max-content-width}
  ), $vars);

  @each $key, $value in $personalities {

    $vars: map-merge((
      --personality-#{$key}: #{$value}
    ), $vars);
  }
  
  $vars: map-merge((
    --personality: var(--personality-2)
  ), $vars);

  
@each $key, $value in $icons {

  $vars: map-merge((
    --icon-#{$key}: #{$value}
  ), $vars);
}


$vars: map-merge((
  --pattern-colour: rgba(223,227,230,0.5),
  --pattern-stroke-sm: 24px,
  --pattern-stroke-default: 32px,
  --pattern-stroke-lg: 28px,
  --pattern-stroke-xl: 26px,
), $vars);


$vars: map-merge((
  --heading-weight: $headings-font-weight
), $vars);