@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.container--product-cards {
  &:not([class*="bg-"]){
    --bg-colour: var(--colour-light);
    @include var(background-color,--colour-light);
  }
}

.product-card {
  --bg-colour: var(--colour-white);
  @include var(background,--colour-white);
  padding: rem(16);
  margin-left: rem(-16);
  margin-right: rem(-16);
  margin-bottom: rem(16);
  box-shadow: 0 rem(4) rem(16) 0 rgba(0,0,0,0.08);

  &:last-child(){
    margin-bottom: 0;
  }

  &:hover,
  &:focus-within {

    box-shadow: 0 rem(8) rem(20) 0 rgba(0,0,0,0.25);
  }

  &:last-child {
    margin-bottom: rem(32);
  }

  @include media-breakpoint-up(sm) {
    padding: rem(32) rem(24);
  }

  .container--product-cards:not(.product-cards--vertical) & {
    @include media-breakpoint-up(sm) {
      padding: rem(32) rem(24);
      margin-left: rem(-24);
      margin-right: rem(-24);
    }

    @include media-breakpoint-up(md) {
      padding: rem(32);
      margin-left: 0;
      margin-right: 0;
    }
  }
}



// #region Product card HEADER
.product-card__header {

  .product-card__title {
    @include var(color,--colour-primary);
    position: relative;
    display: inline-block;
    width: auto;
  }

  a:hover,
  a:focus {
    text-decoration: none;
    
    .product-card__title {
      
      text-decoration: underline;
    }
  }

  .product-card__logo {
    max-height: rem(80);
    min-width: rem(96);
    max-width: rem(96);
  
    @include media-breakpoint-up(md) {

      max-width: min-content;

      img {
        height: rem(64);
        margin-bottom: rem(16);
        width: auto;
        object-fit: contain;
      }
    }
  }

  .container--product-cards:not(.product-cards--vertical) & {
    @include media-breakpoint-up(sm) {
      padding-bottom: rem(8);
    }
  }
}
// #endregion

// #region Product card BODY
.product-card__body {
  position: relative;
  margin-bottom: rem(32);


  .container--product-cards:not(.product-cards--vertical) & {
    @include media-breakpoint-up(sm) {
      padding-bottom: rem(8);
    }
    @include media-breakpoint-up(md) {

      margin-right: -0.75rem;
      margin-left: -0.75rem;

      .col-md {
        min-width: 20%;
        max-width: 33.3333%;
        max-width: rem(320);
        padding-left: 0.75rem;
        padding-right: 0.75rem;
      }

      .col-md:nth-child(5) ~ .col-md{

        max-width: 20%;
      }
    }
  }
}

.product-card__body:after{
  content: "";
  position: absolute;
  bottom: 0;
  left: rem(8);
  width: calc(100% - #{rem(16)});
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);
}
// #endregion

// #region Product card DETAIL
.product-card-detail {
  padding-bottom: rem(16);

  &--img {
    max-width: rem(120)!important;
    min-width: rem(120)!important;
  }
}
// #endregion

// #region Rep example
.rep_example .col-sm{

  .container--product-cards:not(.product-cards--vertical) & {
    @include media-breakpoint-up(sm) {
      min-width: 33.33%;
    }
    @include media-breakpoint-up(md) {
      min-width: 0%;
    }
  }
}
// #endregion

// #region Product card FOOTER
.product-card__footer {


  .btn-group {

    margin: 0;
    padding: 0;

    a:last-child {

      margin-bottom: 0;
    }
  }

  .container--product-cards:not(.product-cards--vertical) & {
    .btn-group {

      @include media-breakpoint-up(sm) {
  
        justify-content: flex-end;
        margin-right: #{rem(-8)};
        width: auto;
        flex-wrap: nowrap;
  
        a {
  
          margin-bottom: 0;
          white-space: nowrap;
        }
      }
    }
  }
}
// #endregion

// #region Vertical product cards
@include media-breakpoint-up(sm) {

  .product-cards--vertical .product-card__wrapper {
    margin-left: rem(-16);
    margin-right: rem(-16);
    display: flex;
    flex-flow: row wrap;
    justify-content: center;

    .product-card__logo {
      order: 2;
      min-width: rem(96)!important;
      max-width: rem(96)!important;
      padding: 0;
    }

    .product-card {
      margin: 0 rem(16) rem(32) rem(16);
      width: calc(50% - #{rem(16 + 16)});
      display: flex;
      flex-direction: column;

      .product-card__body:not(.rep_example) {
          display: block;
          flex-grow: 1;
      }
    }


    .col-3 {
      flex: 0 0 25%!important;
      max-width: 25%!important;
    }
    .col-6 {
      flex: 0 0 50%!important;
      max-width: 50%!important;
    } 
    .col-12 {
      flex: 0 0 100%!important;
      max-width: 100%!important;
      width: 100%!important;
    }
    .text-sm-right {
      text-align: left!important;
    }
    .justify-content-end {
      justify-content: flex-start !important;
    }
    .product-card__footer .btn-group {
      margin-left: rem(-8);
      margin-right: rem(-8);
      display: block;
    }
    .product-card__footer .btn-group a:last-child {
      margin-bottom: 0;
    }


  }
}

@include media-breakpoint-up(md) {
  .product-cards--vertical:not(.product-cards--vertical-3-cols) {
    padding: rem(80) rem(192) rem(64) rem(192);
  }

  .product-cards--vertical-3-cols .product-card__wrapper {
    margin-left: rem(-8);
    margin-right: rem(-8);
  }
  .product-cards--vertical-3-cols .product-card__wrapper .product-card {
    
    margin: 0 rem(8) rem(32) rem(8);
    width: calc(33% - #{rem(16 + 16)});
    max-width: calc(50% - #{rem(16 + 16)});
    flex-grow: 1;
  }
}
// #endregion
