
$font-body: Inter,'Inter Full','Inter Static',Helvetica,Arial,sans-serif;
$font-heading: VirginMoneySans, Helvetica,Arial,sans-serif;
$font-loop: VirginMoneyLoop, Helvetica, Arial, sans-serif;

$fonts: ();
$fonts: map-merge((
  "loop":   $font-loop,
  "heading":   $font-heading,
  "body":   $font-body
), $fonts);

// Boostrap font variables
// We need to match these up with our fonts so that we don't have rogue fonts
$font-family-sans-serif: Inter,Helvetica,Arial,sans-serif;
$font-family-monospace: Inter,Helvetica,Arial,sans-serif;
$font-family-base: Inter,Helvetica,Arial,sans-serif;


// Headings
$headings-font-weight: 700;
$headings-line-height: 1;


$personalities: ();
$personalities: map-merge((
  0: '"ss18"',
  1: '"dlig","ss18"',
  2: '"calt" off',
  3: '"calt"',
  4: '"calt", "dlig"',
  5: '"ss01", "dlig"',
), $personalities);

