@import "../bootstrap/scss/reboot"; // Reboot
@import "../bootstrap/scss/utilities";

@import "_functions";

@import "framework/01-styles/root";

// Print utilities
@each $key, $utility in $utilities {
  // The utility can be disabled with `false`, thus check if the utility is a map first
  // Then check if the utility needs print styles
  @if type-of($utility) == "map" and map-get($utility, print) == true {
    @include generate-utility($utility, "-print");
  }
}

/* Basic reboots */
strong,
.strong {
  font-weight: bold;
}

.d-block {
  display: block;
}

body {
  padding-top: 1rem;
}

/* Hide things */
a.sr-only[href="#content"],
svg.sr-only,
.svg__wrapper,
.pattern__inner,
#nav,
footer {
  display: none!important;
}

/* Images */
img {
  width: auto;
  height: 100%;
  max-height: 20rem;
  margin-bottom: 3rem;
}

/* links */
a[href] {
  display: block;
  margin-bottom: 1rem;
}

a.card[href] {
  margin-bottom: 2rem;

  img {
    margin-bottom: 1rem;
  }
}

@import "framework/05-components/charts.scss";
.chart .chart__x-axis ul .group {
  position: relative;
}

@import "framework/05-components/gauge.scss";

@import "framework/05-components/product-card.scss";

.container--product-cards {
  padding-left: 0.5em!important;
  padding-right: 0.5em!important;
}

.product-card {
  width: 100%!important;
}