:root {
  // Custom variable values only support SassScript inside `#{}`.
  @each $var, $value in $vars {
    #{$var}: #{$value};
  }

  // Default container spacing
  --bg-colour: var(--colour-white);

  // Default container spacing
  --spacing-container: var(--spacing-xs);

  @include media-breakpoint-up(sm) {
    --spacing-container: var(--spacing-sm);
  }

  @include media-breakpoint-up(md) {
    --spacing-container: var(--spacing-md);
  }  
  
  // Default type spacing
  --spacing-type: var(--spacing-xs);

  @include media-breakpoint-up(md) {
    --spacing-type: var(--spacing-sm); // Using $spacing-sm is not a mistake
  }

  --bg-pagination-next: var(--colour-secondary);
  --bg-pagination-prev: var(--colour-secondary-tint);
  --colour-pagination: #ffffff;
  --pattern-scale-adjust: 1;
}

// #region Colour overrides for light coloured backgrounds
[class*="bg-"]:not(.invert-colours):not(.bg-primary):not(.bg-secondary):not(.bg-secondary-3):not(.bg-dark):not(.bg-dark-2) {

  --colour-heading: var(--colour-black);

  @if $compatible == 'true' {

    h1, h2,
    .h1, .h2 {
      @include var(color,--colour-black);
    }
  }
}
// #endregion

// #region Colour overides for darker backgrounds
#{$dark-bg-selector}{
  
  --colour-input: transparent;
  --colour-link: #ffffff;
  --colour-hover: #ffffff;
  --colour-focus: #ffffff;
  --colour-heading: #ffffff;
  --colour-body: #ffffff;
  --colour-selected: #ffffff;
  --colour-active: var(--colour-border);
  color: var(--colour-body);

  @if $compatible == 'true' {
    color: #ffffff;
    @include headers($includeClasses: 'true'){
      color: #ffffff;
    }

    .form-control, .custom-select {
      background-color: transparent;
    }
    .form--submitted .form-group--error {
      border-color: #ffffff;
    }

    .form--submitted .form-group--error .form-control,
    .form--submitted .form-group--error .custom-select {
      border-color: #ffffff!important;
    }

    .form--submitted .form-group--error,
    .form--submitted .form-group--error .error:not([aria-hidden="true"]) {
      color: #ffffff;
    }
    
    a:not(.card), .tooltip {
      color: #ffffff;

      &:hover,
      &:focus,
      &.hover,
      &.focus {
        color: #ffffff;
      }

      &:active,
      &.active {
        @include var(color, --colour-border);
      }
    }
  }
}
// #endregion

// #region Reset cards when on a dark background
#{$dark-bg-selector-base}{

  .card:not(:hover):not(:focus):not(:active) {

    @if $compatible == 'true' {
        
      h1,
      .h1,
      h2,
      .h2 {
        
        @include var(color,--colour-body,!important);
      }
    }
  }
  @if $compatible == 'true' {
    .card:hover:not(:active),
    .card:focus:not(:active),
    .card:active  {
      h1, h2, h3  {
        @include var(color,--colour-primary,!important);
      }
    }
  }
  
  .product-card {


    @if $compatible == 'true' {
        
      h1,
      .h1,
      h2,
      .h2,
      .product-card__title {
        
        @include var(color,--colour-heading,!important);
      }
    }
  }

  .product-card, .card {

    @each $color, $value in $theme-colors {

      --colour-#{$color}: #{$value};
    }
    @each $color, $value in $non-theme-colors {
    
      --colour-#{$color}: #{$value};
    }

    @include var(color,--colour-body);

    [data-tooltip] {

      @include var(background,--colour-light,!important);
    }

    @if $compatible == 'true' {
        
        
      @include headers($includeClasses: 'true'){
        @include var(color,--colour-body,!important);
      }

      .tooltip,
      a {

        @include var(color,--colour-link);

        &:hover:not(:active),
        &:focus:not(:active) {
          @include var(color,--colour-hover);
        }

        &:active  {
          @include var(color,--colour-active);
        }

        &.selected,
        .js-enabled &[data-toggle='collapse']:not(.collapsed){
          @include var(color,--colour-selected);
        }
      }

      .btn-primary{
        @include btn-colours(--colour-white, --colour-primary, --colour-primary, $state:'default',$important:'!important');
        @include btn-colours(--colour-white, --colour-primary-light, --colour-primary-light, $state:'hover',$important:'!important');
        @include btn-colours(--colour-white, --colour-primary-dark, --colour-primary-dark, $state:'active',$important:'!important');
      }
      
      .btn-secondary {
        @include btn-colours(--colour-primary, transparent, --colour-primary, $state:'default',$important:'!important');
        @include btn-colours(--colour-white, --colour-primary, --colour-primary, $state:'hover',$important:'!important');
        @include btn-colours(--colour-white, --colour-primary-dark, --colour-primary-dark, $state:'active',$important:'!important');
      }
      
      .btn-tertiary {
        @include btn-colours(--colour-muted, transparent, --colour-muted, $state:'default',$important:'!important');
        @include btn-colours(--colour-white, --colour-muted, --colour-muted, $state:'hover',$important:'!important');
        @include btn-colours(--colour-white, --colour-dark-2-light, --colour-dark-2-light, $state:'active',$important:'!important');
      }

      .tick-list > li:before {

        background-image: escape-svg($tick-red-svg);
      }
    }
  }
}
// #endregion
