@mixin input-widths($long, $medium, $short, $four, $two, $one, $init: 'false', $compatible: $compatible)
{
  --input-width-long: #{rem($long)};
  --input-width-medium: #{rem($medium)};
  --input-width-short: #{rem($short)};
  --input-width-four: #{rem($four)};
  --input-width-two: #{rem($two)};
  --input-width-one: #{rem($one)};
  
  @if $init == 'true' and $compatible == 'false' {

    .form-control,
    .form-control__wrapper {
      max-width: var(--input-max-width, 100%);
    }

    &--long {

      --input-max-width: var(--input-width-long);
    }
    
    &--medium {
      
      --input-max-width: var(--input-width-medium);
    }

    &--short {
      
      --input-max-width: var(--input-width-short);
    }

    &--4-chars {

      --input-max-width: var(--input-width-four);
    }

    &--2-chars {

      --input-max-width: var(--input-width-two);
    }
    
    &--1-char {

      --input-max-width: var(--input-width-one);
    }

    .form-control--long {

      --input-max-width: var(--input-width-long);
    }
    
    .form-control--medium {
      
      --input-max-width: var(--input-width-medium);
    }

    .form-control--short {
      
      --input-max-width: var(--input-width-short);
    }

    .form-control--4-chars {

      --input-max-width: var(--input-width-four);
    }

    .form-control--2-chars {

      --input-max-width: var(--input-width-two);
    }
    .form-control--1-chars {

      --input-max-width: var(--input-width-one);
    }
  }

  @if $compatible == 'true' {

    &--long .form-control,
    &--long .form-control__wrapper  {

      max-width: rem($long);
    }
    
    &--medium .form-control, 
    &--medium .form-control__wrapper {
      
      max-width: rem($medium);
    }

    &--short .form-control,
    &--short .form-control__wrapper {
      
      max-width: rem($short);
    }

    &--4-chars .form-control,
    &--4-chars .form-control__wrapper {

      max-width: rem($four);
    }

    &--2-chars .form-control,
    &--2-chars .form-control__wrapper {

      max-width: rem($two);
    }

    .form-control--long {

      max-width: rem($long);
    }
    
    .form-control--medium {
      
      max-width: rem($medium);
    }

    .form-control--short {
      
      max-width: rem($short);
    }

    .form-control--4-chars {

      max-width: rem($four);
    }

    .form-control--2-chars {

      max-width: rem($two);
    }

    .form-control--1-char {

      max-width: rem($one);
    }
  }
}