// Default Bootstrap Theme vars
$primary:       #E10A0A;
$secondary:     #D03788;
$success:       #D3E629;
$info:          #DFE3E6;
$warning:       #FF7300;
$danger:        $primary;
$light:         #FAFAFA;
$dark:          #051441;

// VM vars
$primary-highlight:  #B40C0E;
$primary-soft:  #E8364B;
$colour-body: #000000;
$colour-muted: #6D7173;
$colour-secondary-2: #69E1E6;
$colour-secondary-3: #A500F5;
$colour-secondary-4: #FF7300;
$colour-dark-2: #1E2332;
$colour-accent: #383DFF;
$colour-success-tint: #FFFF64;
$colour-success-dark: #9EB400;

// Alert backgrounds @20% 
$info-bg:       #e6f0f2;

// Colour tints 
$colour-primary-light: #E73B3B;
$colour-primary-tint: #C20000;
$colour-secondary-light: #FF6DB8;
$colour-secondary-tint: #9B005B;
$colour-secondary-2-tint: #A0FFFF;
$colour-secondary-3-tint: #DC52FF;
$colour-secondary-4-tint: #FFA441;
$colour-dark-tint: #33396C;
$colour-warning-tint:       #FFA441;
$colour-dark-2-tint: #00000B;
$colour-faded-white: rgba(255,255,255,0.1);

// Functional/state variables 
$colour-border: $info;
$colour-link: $colour-body;
$colour-focus: $primary;
$colour-hover: $primary;
$colour-active: $colour-primary-tint;

$colour-selected: $primary;
$colour-disabled: tbd;
$colour-heading: $primary;


$theme-colors: ();
$theme-colors: map-merge((
  "primary":    $primary,
  "secondary":  $secondary,
  "secondary-2":  $colour-secondary-2,
  "secondary-3":  $colour-secondary-3,
  "secondary-4":  $colour-secondary-4,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "light":      $light,
  "dark":       $dark,
  "dark-2":  $colour-dark-2,
  "white": #FFFFFF,
  "black": $colour-body
), $theme-colors);


// Colours that we want setting but not used for Bootstraps mixins, i.e. .text-primary, .bg-muted
$non-theme-colors: ();
$non-theme-colors: map-merge((
  "muted":      $colour-muted,
  "body":             $colour-body,
  "border":           $colour-border,
  "link":             $colour-link,
  "focus":            $colour-focus,
  "hover":            $colour-hover,
  "hover-alt":        $colour-primary-light,
  "active":           $colour-active,
  "selected":         $colour-selected,
  "input": #FFFFFF,
  "heading": $colour-heading,
  "accent": $colour-accent,
  "primary-light": $colour-primary-light,
  "primary-tint": $colour-primary-tint,
  "secondary-light":  $colour-secondary-light,
  "secondary-tint":  $colour-secondary-tint,
  "secondary-2-tint":  $colour-secondary-2-tint,
  "secondary-3-tint":   $colour-secondary-3-tint,
  "secondary-4-tint":   $colour-secondary-4-tint,
  "dark-tint":   $colour-dark-tint,
  "dark-2-tint":  $colour-dark-2-tint,
  "faded-white":  $colour-faded-white, 
  "success-tint":  $colour-success-tint, 
  "success-dark":  $colour-success-dark, 
), $non-theme-colors);

// Colours that we want setting but not used for Bootstraps mixins, i.e. .text-primary, .bg-muted
$alert-colors: ();
$alert-colors: map-merge((
  "alert":                $light,
  "alert-highlight":      $colour-border,
  "alert-success":                #{rgba($success,0.5)},
  "alert-success-highlight":      $success,
  "alert-error":      $primary,
  "alert-error-highlight":      #{rgba($colour-primary-light,0.5)},
  "alert-warning":      $colour-warning-tint,
  "alert-warning-highlight":      $warning,
), $alert-colors);

$white:    #ffffff;
$gray-100: $light;
$gray-200: $light;
$gray-300: $colour-border;
$gray-400: $colour-border;
$gray-500: $colour-muted;
$gray-600: $colour-muted;
$gray-700: $colour-body;
$gray-800: $colour-body;
$gray-900: $colour-body;
$black: $colour-body;

$grays: ();
$grays: map-merge((
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900
), $grays);

